.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem 2rem;
  background-color: #f0f0f0;
}

.logo {
  width: 150px; /* Adjust width as needed */
}

.header__nav {
  display: flex; /* Sets horizontal layout */
  list-style: none; /* Removes default bullet points */
  margin: 0;
  padding: 0;
}

.header__nav li {
  margin-right: 1rem; /* Adjust spacing as needed */
  display: inline-block; /* Optional for spacing */
}

.header__nav a {
  text-decoration: none;
  color: #333;
  font-weight: bold;
}

.header__nav a:hover {
  color: #000;
}
