.footer {
    position: fixed;
    bottom: 0;
    width: 100%;
    background-color: #000;
    color: #fff;
    padding: 2rem 0;
    text-align: center;
  }

.footer__container {
  display: flex;
  flex-direction: column;
  align-items: center; /* Center content horizontally */
  width: 80%; /* Adjust width as needed */
  margin: 0 auto; /* Center container horizontally */
}

.footer__contact-info {
  display: flex;
  justify-content: space-between; /* Space sections horizontally */
  width: 100%; /* Take full width of container */
  margin-bottom: 2rem;
}

.footer__contact-us {
  display: flex;
  align-items: center;
}

.footer__logo {
  width: 100px; /* Adjust width as needed */
  margin-right: 1rem;
}

.footer__contact-details {
  line-height: 1.5; /* Adjust line spacing as needed */
}

.footer__industries {
  text-align: left; /* Align text to the left */
}

.footer__industries h3 {
  margin-bottom: 1rem;
}

.footer__industries-divider {
  height: 1px; /* Adjust divider height as needed */
  background-color: #fff;
  margin-bottom: 1rem;
}

.footer__industries-list {
  list-style: none;
  padding: 0;
  margin: 0;
}

.footer__industries-list li {
  margin-bottom: 0.5rem;
}

.footer__arrow {
  font-size: 1rem; /* Adjust arrow size as needed */
  margin-right: 0.5rem;
}

.footer__social-media {
  display: flex;
  justify-content: center; /* Center icons horizontally */
  margin-top: 2rem;
}

.footer__social-icon {
  width: 30px; /* Adjust icon size as needed */
  margin: 0 1rem;
}
